.header-profile-coins-and-more {
    background: #1D1D1F;
    height:50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 12px 16px;
}
.header-profile-coins-and-more > svg{
    cursor: pointer;
}
.header-profile-coins {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 7px 12px;
    gap: 4px;
    background: #272729;
    border-radius: 100px;
    cursor: pointer;
}
.header-profile-coins > span {
    font-weight: 600;
    font-size: 14px;
    line-height: 18px;
    text-align: center;
    letter-spacing: -0.154px;
    color: #FFFFFF;
    user-select: none;
}

.header-profile-main-container {
    display: grid;
    grid-template-columns: 50% calc(50% - 80px) calc(64px + 16px);
    grid-template-rows: calc(40px - 16px) calc(20px + 20px) auto;
    background: #1D1D1F;
    border-radius: 0px 0px 16px 16px;
    padding: 16px;
    row-gap: 16px;
}

.header-profile-img {
    width: auto;
    height: 80px;
    grid-row: 1 / span 2;
    grid-column: 3;
    background-color: #7F8185;;
}

.header-profile-info {
    line-height: 20px;
    grid-column: 1 / span 2;
}
.header-profile-info > h2 {
    font-style: normal;
    font-weight: 800;
    font-size: 20px;
    line-height: 28px;
    color: #FFFFFF;
    margin: 0px;
}
.header-profile-img {
    float: right;
    width: auto;
    height: 100%;
    /* border: 8px solid #1D1D1F; */
    border-radius: 50%;
}
.header-profile-counter {
    display: flex;
    gap: 16px;
    /* margin-top: 16px; */
}
.profile-counter {
    display: flex;
    flex-direction: column;
}
.profile-counter > p {
    font-style: normal;
    font-weight: 800;
    font-size: 17px;
    line-height: 22px;
    color: #FFFFFF;
    margin: 0px;
}

.profile-counter > span {
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 18px;
    color: #7F8185;
}

.header-prifle-share {
    display: flex;
    grid-row: 3;
    grid-column: 1 / span 3;
    margin-top: 8px;
    justify-content: stretch;
    align-items: center;
    gap: 12px;
}

.header-profile-share-link {
    height: 48px;
    width: 100%;
    background: #272729;
    border-radius: 12px;
    padding: 13px 16px;
    box-sizing: border-box;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #7F8185;
}
.header-profile-share-link::selection{
    color: #62D962;
    background-color: initial;
}

.header-profile-share-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 48px;
    min-height: 48px;
    background: #272729;
    border-radius: 12px;
    cursor: pointer;
}

#empty-profile-header-back {
    position: relative;
    height: 150px;
    /* padding: 100px 0px 16px; */
    background: rgba(0, 0, 0, 0.32);
    backdrop-filter: blur(48px);
    background-color: #1D1D1F;
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: 50%;
    filter: blur(20px);
}

#empty-profile-container {
    margin-top: -15px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: #1D1D1F;
    border-radius: 16px;
    z-index: 99999999;
    padding: 0px 16px 16px 16px;
}

#main-empty-profile-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
#empty-profile-text {
    margin-top: 16px;
    margin-bottom: 24px;
}
#empty-profile-text h2 {
    margin: 0px;
    font-style: normal;
    font-weight: 800;
    font-size: 20px;
    line-height: 28px;
    text-align: center;
    color: #FFFFFF;
}
#empty-profile-avatar {
    width: 96px;
    height: 96px;
    margin-top: -48px;
    border: 8px solid #1D1D1F;
    border-radius: 50%;
    box-sizing:content-box;
    background-color: #7F8185;
}