#share-photo-storis {
    display: flex;
    justify-content: center;
    margin-bottom: 34px;
}

#button-share-link-and-save {
    display: flex;
    flex-direction: column;
    margin-bottom: 34px;
}

#button-share-link-and-save h2 {
    font-style: normal;
    font-weight: 800;
    font-size: 20px;
    line-height: 28px;
    color: white;
    padding: 0px;
    margin-bottom: 18px;
}

.share-link-and-save {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background: #272729;
    border-radius: 16px;
    padding: 17px 16px;
    cursor: pointer;
    user-select: none;
}

.share-link-and-save > span {
    font-style: normal;
    font-weight: 800;
    font-size: 17px;
    line-height: 22px;
    color: #7F8185;
}

#share-link-and-save-button-container {
    display: flex;
    flex-direction: column;
    gap: 16px;
}